<template>
	<div class="participateNumber">
		<div class="participateNumber_header">
			<i class="icon_arrow_left" @click="goBack"></i>
			<p class="participateNumber_title">我的参与编号</p>
		</div>
		<div class="participateNumber_main">
			<ul class="goods_list">
				<li>
					<img :src="item.commodityMainImg" alt="" class="img_goods">
					<div class="goods_list_box">
						<p class="text1">
							<img :src="item.logo" alt="" class="icon_logo">
							<span>{{ item.commodityName }}</span>
						</p>
						<p class="text2">
							<i class="icon_time"></i>
							<span>开奖时间：{{ item.endTimeStr }}</span>
						</p>
						<p class="text3">
							<i class="icon_gift"></i>
							<span>共{{ item.quantity }}份奖品</span>
							<i class="icon_heat"></i>
							<span>已参与{{ myNumberObj.totalCount }}次</span>
						</p>
						<p class="text4"><span>我共参与了</span><span class="act">{{ myNumberObj.userCycleInvestmentCount }}</span><span>次</span></p>
					</div>
					<p class="goods_list_stage">第{{ item.cycle }}期</p>
				</li>
			</ul>
			<div class="participateNumber_main_number">
				<p class="participateNumber_main_number_title">参与编号</p>
				<ul class="numberList">
					<li v-for="(item,index) in myNumberObj.numberList" :key="index">
						<span>{{ item }}</span>
					</li>
				</ul>
			</div>
		</div>
		<div v-show="adLoadingFlag" class="shadow">
			<van-loading size="36px" vertical text-color="#ffffff" color="#ffffff">加载中...</van-loading>
		</div>
	</div>
</template>

<script>
	export default{
		name:'participateNumber',
		data(){
			return{
				adLoadingFlag:false,
				item:{},
				obj:{},
				myNumberObj:{
					numberList:[]
				},
			}
		},
		mounted() {
			var jsonObj = localStorage.getItem("obj");
			this.obj = JSON.parse(jsonObj);
			this.item = this.$route.query.item;
			this.freeGoodsMyNumber();
		},
		methods:{
			goBack(){
				this.$router.go(-1);
			},
			freeGoodsMyNumber(){
				let _this = this;
				_this.adLoadingFlag = true;
				var exclusiveObj = {
					activityInfoId:_this.item.activityInfoId,
					cycle:_this.item.cycle,
					goodsId:_this.item.goodsId
				};
				
				var requestData = Object.assign(_this.obj,exclusiveObj);
				console.log(requestData);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
					method: 'post',
					url: '/free/goods/myNumber',
					data:redata,
				}).then(function(res){
					if(res.data.code === 200){
						var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						console.log(reData);
						_this.myNumberObj = reData;
					}
					_this.adLoadingFlag = false;
				})
			}
		}
	}
</script>

